.categories {

  &__wrapper {
    display: grid;
    row-gap: 40px;


  @include onTablet {
    row-gap: 64px;
  }
  }

  &__button {
    box-sizing: border-box;
    text-decoration: none;
    display: block;
    font-weight: 700;
    font-size: 16px;
    line-height: normal;
    padding: 14px 48px;
    background-color: $main-text-color;
    color: white;
    font-family: $main-font-family;
    border: 2px solid transparent;
    border-radius: 8px;
    margin-top: 40px;
    text-align: center;
    transition: all 300ms;

    @include onDesktop {
      width: 324px;
     }

    &:hover {
      transform: scale(102%);
      color: $main-text-color;
      border: 2px solid #f0f1f4;
      background-color: $grey-color;
    }

    &:active {
      color: white;
      border: 2px solid transparent;
      background-color: #111;
    }
  }
}


.category {

  &__photos {
    @include pageGrid;
    margin-bottom: 16px;


  }

  &__link {
    position: relative;
    &--wide {
      @include onTablet {
        grid-column: span 4;
      }
      @include onDesktop {
        grid-column: span 8;
      }
    }
    &--square {
      height: 0;
      padding-bottom: 100%;
      @include onTablet {
       grid-column: span 2;
      }
      @include onDesktop {
        grid-column: span 4;
      }
    }
  }
  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include hover(transform, scale(105%));
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
  }
}
