@mixin onTablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}


@mixin contentPaddingInline() {
  padding-inline: 20px;

 @include onTablet {
  padding-inline:72px;
 }

 @include onDesktop {
  max-width: 1020px;
  margin-inline: auto;
  padding-inline: 120px;
}
}

.container {
  @include contentPaddingInline;
}

@mixin hover($property, $toValue) {
  transition: #{$property} $effect-duration;
  &:hover {
    #{$property}: $toValue
  }
}


@mixin pageGrid {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include onTablet {
  --columns: 6;
   column-gap: 24px;
  }

  @include onDesktop {
    --columns: 12;
  }
}


@mixin form-styling {
  display: block;
  box-sizing: border-box;

  color: #7E7E83;
  border: 1px solid transparent;
  outline: none;

  font-family: $font-family-secondary;

  font-size: 14px;
  font-weight: 400;
  line-height: 140%;

  border-radius: 8px;
  background-color: $grey-color;

  @include hover(border, 1px solid #d5d7de);



  &::placeholder {
    color: $font-family-secondary;

    @include hover(color, 0.3s);
  }

  &:hover::placeholder {
    color: $main-text-color;
  }

  &:focus {
    border: 1px solid $main-text-color;;
    color: $main-text-color;
  }

  &:-webkit-autofill,
  &:-webkit-autofill:hover,
  &:-webkit-autofill:focus,
  &:-webkit-autofill:active {
      -webkit-background-clip: text;
      -webkit-text-fill-color: $main-text-color;
      transition: background-color 5000s ease-in-out 0s;
      box-shadow: inset 0 0 20px 20px $grey-color;
  }
}
