.contacts {

  margin-bottom: 64px;

  @include onTablet {
    margin-bottom: 120px;
  }

  &__section {
    @include onDesktop {
      @include pageGrid;
    }
  }

  &__form {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 80px;
    }

    @include onDesktop {
      grid-column: span 5;
      margin-bottom: 0;
    }
  }

  &__contact {
    display: flex;
    flex-direction: column;
    gap: 24px;

    @include onDesktop {
      grid-column: 7 / -1;
    }
  }
}


.form {
  display: flex;
  flex-direction: column;

  &__input {
    @include form-styling;
    margin-bottom: 16px;
    height: 48px;
    padding-left: 14px;
  }

  &__textarea {
    resize: none;
    @include form-styling;
    height: 148px;
    padding-left: 14px;
  }
}

.button {
  box-sizing: border-box;
  text-decoration: none;
  display: block;
  font-weight: 700;
  font-size: 16px;
  line-height: normal;
  padding: 14px 48px;
  background-color: $main-text-color;
  color: white;
  font-family: $main-font-family;
  border: 2px solid transparent;
  border-radius: 8px;
  margin-top: 32px;
  text-align: center;
  transition: all 300ms;

  @include onDesktop {
   width: 411px;
  }

  &:hover {
    transform: scale(102%);
    color: $main-text-color;
    border: 2px solid #f0f1f4;
    background-color: $grey-color;
  }

  &:active {
    color: white;
    border: 2px solid transparent;
    background-color: #111;
  }
}

.contact {

  &__text {
    margin: 0;
    padding: 0;
    color: #9393a3;

    font-size: 14px;
    font-weight: 400;
    line-height: 140%;
    margin-bottom: 8px;
  }

  &__link {
    text-decoration: none;

    color: #191919;

    font-family: $font-family-secondary;

    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    @include hover(color, #7e7e83);
  }
}
