
.menu {
  box-sizing: border-box;

  height: 100vh;
  background-color: #E5E6E8;
  font-weight: 700;
  overflow: auto;

  &__top {
    margin-bottom: 32px;
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #e5e5e5;
    @include onTablet {
     margin-bottom: 56px;
    }
  }

  &__nav{
  margin-bottom: 40px;
  }

  &__phone-number {
   color:inherit;
   text-decoration: none;
   margin-bottom: 16px;
   display: block;
   font-size: 16px;
   line-height: 140%;
   letter-spacing: 2px;
  }

  &__call-to-order {
   color:inherit;
   width: fit-content;
   text-decoration: none;
   display: block;
   font-size: 16px;
   line-height: 140%;
   letter-spacing: 2px;
   text-transform: uppercase;
   padding-bottom: 7px;
   border-bottom: 1px solid $main-text-color;
  }

}

