.header {
  box-sizing: border-box;
  height: 100vh;
  background-image: url(../images/header-bg-mobile.png);
  background-size: cover;
  background-position: center;

  &__content {
    height: 100vh;
    display: flex;

    flex-direction: column;
    justify-content: space-between;

  }

  &__title {
    margin: 0;
    font-size: 36px;
    line-height: 100%;
    max-width: 280px;

    @include onTablet{
      font-size: 48px;
      max-width: 392px;
    }

    @include onDesktop{
      font-size: 56px;
      max-width: 498px;
    }
  }

  &__bottom {
    padding-bottom:40px;

    @include onTablet{
      padding-bottom: 64px;
    }
  }
  }


