.about-us {
  @include pageGrid;
  margin-bottom: 104px;

  @include onTablet {
    margin-bottom: 120px;
  }
  &__title {
    grid-column: 1 / -1;
    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: span 5;
    }
  }

  &__content{
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      grid-column: span 5;
    }
  }

  &__descr {
    font-family: $font-family-secondary;
    font-weight: 400;
    margin: 0;
    font-size: 16px;
    line-height: 22.4px;
  }
}
